nav {
	display: flex;
	float: none;
	padding: 0 30px;
	width: 100%;
	background: #1e7bc3;
	box-sizing: border-box;

	ul {
		display: flex;
		justify-content: space-between;
		width: 100%;
		position: relative;
		left: 0; }

	li {
		position: relative;
		text-decoration: none !important;
		height: auto;
		margin: 0;

		&:before {
			content: "";
			position: absolute;
			bottom: 0;
			left: 0;
			display: block;
			width: 100%;
			height: 3px;
			background: #000;
			transform: translateY(3px);
			transition: transform .3s ease, opacity .3s ease;
			opacity: 0; }

		&:hover {

			&:before {
				transform: translateY(0);
				opacity: 1; } } }


	a {
		display: block;
		padding: 20px 10px;
		white-space: nowrap; }


	> li {
		position: relative;

		// overflow: hidden
		a {}
		text-transform: none !important;
		font-size: rem(15);
		font-weight: 400;

		&:hover {
			ul {
				pointer-events: all;
				opacity: 1; } } } }


#horizontal-multilevel-menu {
	position: relative;
	color: #000;

	ul {
		position: absolute;
		top: 100%;
		left: 0;
		display: flex;
		flex-direction: column;
		opacity: 0;
		pointer-events: none;
		background: #fff;
		z-index: 999;
		color: #000;
		width: auto;
		box-shadow: 1px 1px 9px rgba(0, 0, 0, 0.2);
		padding: 10px 0;

		&:before {
			display: none; }

		li {
			transition: background .3s ease;
			&:before {
				display: none; }

			&:hover {
				background: rgba(30, 123, 195, 0.4); } }


		a {
			color: #000;
			padding: 10px 25px; } }



	> li {
		&:hover {

			ul {
				opacity: 1;
				pointer-events: all; } } } }


